export enum Orientation {
    LANDSCAPE = "landscape",
    PORTRAIT = "portrait"
}

export enum ScaleMode {
    FIT = "fit",
    FILL = "fill",
    STRETCH = "stretch"
}

export interface IGameConfig {
    /**
     *     
     *  ,     
     */
    logicWidth: number;

    /**
     *     
     */
    logicHeight: number;

    /**
     *  
     */
    orientation: Orientation;

    /**
     *  
     */
    scaleMode: ScaleMode;

    /**
     * Renderer settings
     */
    renderer: {
        /**
         *   (hex)
         */
        background: number;

        /**
         *  
         *  ,    FPS
         */
        antialias: boolean;

        /**
         *  device pixel ratio
         *  resolution   
         */
        maxResolution: number;

        /**
         * Auto density -   devicePixelRatio
         */
        autoDensity: boolean;

        /**
         * Auto start ticker
         */
        autoStart: boolean;
    };

    /**
     * CSS   game container
     *  null -   
     *   -    (  )
     */
    container: {
        /**
         *   (CSS)
         * : "100vw", "800px", "80%"
         * null = 
         */
        width: string | null;

        /**
         *   (CSS)
         * : "100vh", "600px", "90%"
         * null = 
         */
        height: string | null;

        /**
         * CSS background  
         */
        cssBackground: string;

        /**
         * Border radius  
         */
        borderRadius: string;

        /**
         *  
         */
        position: "relative" | "absolute" | "fixed";

        /**
         *  true -   100% viewport
         */
        fullscreen: boolean;
    };

    /**
     * Debug 
     */
    debug: {
        /**
         *  FPS
         */
        showFPS: boolean;

        /**
         *  debug  
         */
        showCoordinatePoints: boolean;

        /**
         *    
         */
        verbose: boolean;
    };
}



